.results-list {
  position: absolute;
  top: 2.6rem;
  z-index: 1000;
  left: 0;
  width: 100%;
  background-color: #f5f5f5 !important;
  color: #000 !important;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  margin: 1rem .5rem;
  max-height: 300px;
  overflow-y: auto;
}

 @media (max-width:767px) {

.results-list{
  top: 1.4rem;
}
 }